<template>
    <div class="register-record" v-loading='loading'>
        <div class="content-turn" :style="{'-webkit-overflow-scrolling': scrollMode}">
        	<div v-if='record' class='record'>
        		<img src="./images/aa.png" alt="" />
        		<p>暂无任何记录</p>
        	</div>
            <div class="wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
                <div>
                    <mt-loadmore
                    	:top-method="loadTop"
                        :bottom-method="loadBottom"
                        :bottom-all-loaded="bottomAllLoaded"
                        :autoFill="false"
                        :bottom-loading-text="bottomLoadingText"
                        ref="loadmore">
                        <ul class="same">
                            <li  v-for="(item,index) in list">
                                <p>
                                    <span>转出记录</span>
                                    <span>{{ item.fromAddr }}</span>
                                </p>
                                <p>
                                    <span>转出数量</span>
                                    <span class="red">{{ item.getAmmount }}</span>
                                </p>
                                <p>
                                    <span class="special">转出时间</span>
                                    <span>{{ item.tranferTime | formDate }}</span>
                                </p>
                            </li>
                        </ul>
                    </mt-loadmore>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import ContractServies from '@/services/contract-servies';
import ContractConfig from '@/config/contract-config';
import Vue from 'vue';
var Filters = Vue.filter('date');
    export default {
        //组件名
        name: 'template',
        //实例的数据对象
        data() {
            return {
            	record:false,
                list: [],
                limit: 5,
                page: 0,
                total: 0,
                bottomAllLoaded: false,
                bottomLoadingText: '加载中...',
                wrapperHeight: 0,
                scrollMode:'auto',
                loading:false,
               
            }
        },
        //数组或对象，用于接收来自父组件的数据
        props: {

        },
        //计算
        computed: {

        },
        //生命周期函数
        created() {
//      	alert(this.$route.query.admin);
			this.loading = true;
            this.init(() => {
				this.loading = false;
            });
        },
        beforeMount() {

        },
        mounted() {
            this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
        },
        //方法
        methods: {
            init(fn){
                let page = 0;
                var response = ContractServies.call('JuzixTokenManager','pageByAccount',[this.$route.query.account,1,page,this.limit]);
                console.log('获取转出记录>>>>>>>>>>'+JSON.stringify(response));
//              alert('获取登记记录' );
                if(response.ret == 0){
                	if(response.data.items.length > 0){
                		this.record = false;
                		this.list = response.data.items;
	                    this.total = response.data.total;
	                    this.page = ++page;
	                    this.checkLoaded();
	                    this.getAmount(response.data.items); 
                	}else{
                		this.record = true;
                	};
	                if(fn && typeof fn == 'function'){
	                    fn();
	                }
                }else{

	                if(fn && typeof fn === 'function'){
	                      fn();
	                  }
                }
            },
            loadTop(){
                this.init(()=>{
                	this.loading = false;
                    this.$refs.loadmore.onTopLoaded();
                });
            },
            loadBottom(){
                if(this.checkLoaded()){
                    this.bottomLoadingText = '没有更多了...';
                    setTimeout(() => {
                        this.$refs.loadmore.onBottomLoaded();
                    }, 500);
                    return ;
                }else{
                    this.bottomLoadingText = '加载中...';
                    var res = ContractServies.call('JuzixTokenManager','pageByAccount',[this.$route.query.account,2,this.page,this.limit]);
                    console.log('获取转出记录'+JSON.stringify(res));
                    if(res.ret == 0){
                        this.list  = this.list.concat(res.data.items);
                        this.page = ++this.page
                        this.getAmount(this.list);
                    }
                    this.$refs.loadmore.onBottomLoaded();
                }

            },
            checkLoaded(){
                // this.bottomAllLoaded = this.cache[i].list.length >= this.cache[i].total;
                return this.list.length >= this.total;
            },
            //解密
            getAmount(list){
                let that = this;
                list.forEach(function(data, index){
                    (function(data, index){
                    	//判断是不是管理员
                    	if(that.$route.query.admin == 1){
                    		//管理员
                    		//管理员地址 
                    		if(that.$route.query.account == data.fromAddr){
                    			//解密自己
                    			Was.exec('nizk', 'nizkDecryption', {
		                            ciphertext: data.amountOut,
		                            privateKey: ContractServies.getprivateKeyRandom()
		                        }, (res) => {
		                        	console.log('解密后的数据'+res.data);
		                            if (res.code == 0) {  
		                            	that.$set(that.list[index], 'getAmmount', res.data);
		                            } else {
		                                alert(res.desc);    
		                            }
		                        });
                    		}else{
                    			//密文显示
                    			that.$set(that.list[index], 'getAmmount', data.amountOut.slice(0,6)+'......');
                    		}
                    		
                    	}else if(that.$route.query.admin == 2){
                    		//非管理员 解密自己
                    		Was.exec('nizk', 'nizkDecryption', {
	                            ciphertext: data.amountOut,
	                            privateKey: ContractServies.getprivateKeyRandom()
	                        }, (res) => {
	                        	console.log('依次解密后的数据'+res.data);
	                            if (res.code == 0) {  
	                            	that.$set(that.list[index], 'getAmmount', res.data);
	                            } else {
	                                // console.log(res.desc);
	                                alert(res.desc);    
	                            }
	                        });
                    	}
                    })(data, index);
                })
            },


        },

        //监视
        watch: {

        },
        //组件
        components: {
        
        },
        //过滤器
        filters:{
            formDate(time){
                let date = new Date(parseInt(time));
                return Filters(date,'yyyy-MM-dd hh:mm');
                //此处formatDate是一个函数，将其封装在filter
            }
        },
        //自定义指令
        directive:{

        }
    }
</script>

<!--
	作者：liangyanxiangde@163.com
	时间：2017-03-27
	描述：统一使用less,局部样式
-->
<style lang="less" scoped>
@import "../../less/modules/color.less";
@import "../../less/modules/base.less";
@bgColor:#f4f7f7;
.register-record{
    min-height: 100%;
    padding:.21rem .32rem;
    // background-color:@bgColor;
    padding-top:0;
}
.page-loadmore-wrapper {
    overflow: scroll;
}
.mint-loadmore-bottom{
    span{
        display: inline-block;
        // transition: .2s linear;
        vertical-align: middle;
    }
}
.record{
	height:100%;
	text-align:center;
	font-size:.28rem;
	margin-top:2.13rem;
	color:#CCCCCC;
	img{
		width:1.96rem;
		/*height:1.16rem;*/
	}
}
.wrapper{
    padding-top:.6rem;
    overflow:scroll;
}
ul{
    li{
        padding:.32rem .21rem .10rem;
        background-color:@bgColor;
        border:.01px solid @border3;
        border-radius:.05rem;
        margin-bottom:.21rem;
        p{
            display: flex;
            flex-wrap:nowrap;
            span{
                margin-top:.1rem;
                &:first-child{
                    display: inline-block;
                    height: .90rem;
                    width:1.60rem;
                    color:#999;
                    font-size:.30rem;
                    margin-right:.34rem;
                }
                &:last-child{
                    // line-height: 90rem/@base;
                    display: inline-block;
                    width:4.70rem;
                    line-height: .26rem;
                    word-wrap: break-word;
                    word-break:break-all;

                }

            }
            .red{
                color:red;
            }

        }

    }
}


</style>


